module.exports = {"markup":"<h1 id=\"matchPath\"><a href=\"/native/api/matchPath\" class=\"internal-link\">matchPath</a></h1>\n<p>This lets you use the same matching code that <code>&lt;Route&gt;</code> uses except outside of the normal render cycle, like gathering up data dependencies before rendering on the server.</p>\n<pre><code class=\"language-js\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> matchPath <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router&apos;</span>\n\n<span class=\"token keyword\">const</span> match <span class=\"token operator\">=</span> <span class=\"token function\">matchPath</span><span class=\"token punctuation\">(</span><span class=\"token string\">&apos;/users/123&apos;</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span>\n  path<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;/users/:id&apos;</span><span class=\"token punctuation\">,</span>\n  exact<span class=\"token punctuation\">:</span> <span class=\"token boolean\">true</span><span class=\"token punctuation\">,</span>\n  strict<span class=\"token punctuation\">:</span> <span class=\"token boolean\">false</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n</code></pre>\n<h2 id=\"matchPath-pathname\"><a href=\"/native/api/matchPath/pathname\" class=\"internal-link\">pathname</a></h2>\n<p>The first argument is the pathname you want to match. If you&#x2019;re using\nthis on the server with Node.js, it would be <code>req.path</code>.</p>\n<h2 id=\"matchPath-props\"><a href=\"/native/api/matchPath/props\" class=\"internal-link\">props</a></h2>\n<p>The second argument are the props to match against, they are identical\nto the matching props <code>Route</code> accepts:</p>\n<pre><code class=\"language-js\"><span class=\"token punctuation\">{</span>\n  path<span class=\"token punctuation\">,</span> <span class=\"token comment\">// like /users/:id</span>\n  strict<span class=\"token punctuation\">,</span> <span class=\"token comment\">// optional, defaults to false</span>\n  exact <span class=\"token comment\">// optional, defaults to false</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n","headers":[{"text":"pathname","slug":"pathname"},{"text":"props","slug":"props"}],"title":{"text":"matchPath","slug":"matchPath"}}

